import React, { Component } from 'react';
import { DragSource, DropTarget } from 'react-dnd';
import PropTypes from 'prop-types';
import flow from 'lodash/flow';
import ClassNames from 'classnames';
import {
  tagSource,
  tagTarget,
  dragSource,
  dropCollect,
} from './DragAndDropHelper';
import { canDrag } from './utils';

import RemoveComponent from './RemoveComponent';

const ItemTypes = { TAG: 'tag' };

class Tag extends Component {
  render() {
    const { props } = this;
    const label = props.tag[props.labelField];
    const {
      connectDragSource,
      isDragging,
      connectDropTarget,
      isAdmin,
      tag,
      classNames,
    } = props;
    const { className = '' } = tag;
    const tagComponent = (
      <button
        className={ClassNames('tag-wrapper', classNames.tag, className)}
        style={{
          opacity: isDragging ? 0 : 1,
          cursor: canDrag(props) ? 'move' : 'pointer',
        }}
        onClick={props.onTagClicked}
        onKeyDown={props.onTagClicked}
        onTouchStart={props.onTagClicked}>
        <span className="tag-name">{label}</span>
        <RemoveComponent
          tag={props.tag}
          className={classNames.remove}
          removeComponent={props.removeComponent}
          isAdmin={isAdmin}
          onClick={props.onDelete}
        />
      </button>
    );
    return connectDragSource(connectDropTarget(tagComponent));
  }
}

Tag.propTypes = {
  labelField: PropTypes.string,
  onDelete: PropTypes.func.isRequired,
  tag: PropTypes.shape({
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
  }),
  moveTag: PropTypes.func,
  removeComponent: PropTypes.func,
  onTagClicked: PropTypes.func,
  classNames: PropTypes.object,
  isAdmin: PropTypes.bool,
  connectDragSource: PropTypes.func.isRequired,
  isDragging: PropTypes.bool.isRequired,
  connectDropTarget: PropTypes.func.isRequired,
};

Tag.defaultProps = {
  labelField: 'text',
  isAdmin: true,
};

export default flow(
  DragSource(ItemTypes.TAG, tagSource, dragSource),
  DropTarget(ItemTypes.TAG, tagTarget, dropCollect)
)(Tag);
